<template>
  <div class="content__catalog">
    <aside class="filter">
      <h2 class="filter__title">Фильтры</h2>

      <form class="filter__form form"
            @submit.prevent="submit"
            action="#"
            method="get"
      >
        <fieldset class="form__block">
          <legend class="form__legend">
            Цена
          </legend>
          <label class="form__label form__label--price">
            <input class="form__input"
                   type="text"
                   name="min-price"
                   v-model.number="currentPriceFrom"
            >
            <span class="form__value">От</span>
          </label>
          <label class="form__label form__label--price">
            <input class="form__input"
                   type="text"
                   name="max-price"
                   v-model.number="currentPriceTo">
            <span class="form__value">До</span>
          </label>
        </fieldset>

        <fieldset class="form__block">
          <legend class="form__legend">Категория</legend>
          <label class="form__label form__label--select">
            <select class="form__select"
                    type="text"
                    name="category"
                    v-model.number="currentCategoryId"
            >
              <option value="0">Все категории</option>
              <option :value="category.id"
                      v-for="category in categories"
                      :key="category.id">
                {{category.title }}
              </option>
            </select>
          </label>
        </fieldset>

        <fieldset class="form__block">
          <legend class="form__legend">Цвет</legend>
          <ul class="colors">
            <li class="colors__item" v-for="color in colors" :key="color.id">
              <label class="colors__label">
                <input class="colors__radio sr-only"
                       type="radio"
                       name="color"
                       :value="color.id"
                       v-model="colorsFilter"
                >
                <span class="colors__value" :style="`background-color: ${color.code};`">
                  </span>
              </label>
            </li>
          </ul>
        </fieldset>

        <fieldset class="form__block">
          <legend class="form__legend">Объемб в ГБ</legend>
          <ul class="check-list">
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only"
                       type="checkbox"
                       name="volume"
                       value="8"
                       checked=""
                >
                <span class="check-list__desc">
                    8
                    <span>(313)</span>
                  </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only"
                       type="checkbox"
                       name="volume"
                       value="16"
                >
                <span class="check-list__desc">
                    16
                    <span>(461)</span>
                  </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only"
                       type="checkbox"
                       name="volume"
                       value="32"
                >
                <span class="check-list__desc">
                    32
                    <span>(313)</span>
                  </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only"
                       type="checkbox"
                       name="volume"
                       value="64"
                >
                <span class="check-list__desc">
                    64
                    <span>(313)</span>
                  </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only"
                       type="checkbox"
                       name="volume"
                       value="128"
                >
                <span class="check-list__desc">
                    128
                    <span>(313)</span>
                  </span>
              </label>
            </li>
            <li class="check-list__item">
              <label class="check-list__label">
                <input class="check-list__check sr-only"
                       type="checkbox"
                       name="volume"
                       value="264"
                >
                <span class="check-list__desc">
                    264
                    <span>(313)</span>
                  </span>
              </label>
            </li>
          </ul>
        </fieldset>

        <button class="filter__submit button button--primery"
                type="submit"
        >
          Применить
        </button>
        <button class="filter__reset button button--second"
                type="button"
                @click.prevent="reset"
        >
          Сбросить
        </button>
      </form>
    </aside>
  </div>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      colorsFilter: '',

      categoriesData: null,
      colorsData: null,
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorFilter'],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(val) {
      this.currentPriceFrom = val;
    },
    priceTo(val) {
      this.currentPriceTo = val;
    },
    categoryId(val) {
      this.currentCategoryId = val;
    },
    colorFilter(val) {
      this.colorsFilter = val;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:colorFilter', this.colorsFilter);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:colorFilter', '');
    },
    loadCategoriesId() {
      axios.get(`${API_BASE_URL}/api/productCategories`)
        .then((response) => {
          this.categoriesData = response.data;
        });
    },
    loadColorsProduct() {
      axios.get(`${API_BASE_URL}/api/colors`)
        .then((response) => {
          this.colorsData = response.data;
        });
    },
  },
  created() {
    this.loadCategoriesId();
    this.loadColorsProduct();
  },
};
</script>
